import { Component, OnInit } from "@angular/core";
import { UserService } from "src/app/_services/user.service";
import { AlertifyService } from "src/app/_services/Alertify.service";
import { User } from "src/app/_models/user";
import { ActivatedRoute } from "@angular/router";
import { Pagination, PaginatedResult } from "src/app/_models/pagination";

@Component({
  selector: "app-member-list",
  templateUrl: "./member-list.component.html",
  styleUrls: ["./member-list.component.css"],
})

export class MemberListComponent implements OnInit {
  users: User[];
  user: User = JSON.parse(localStorage.getItem("user"));

  genderList = [
    { value: "masculino", display: "Masculino" },
    { value: "feminino", display: "Feminino" },
  ];
  ufList = [
    "Acre", "Alagoas", "Amazonas", "Amap", "Bahia", "Cear", "Distrito Federal", "Esprito Santo", "Gois", "Maranho",
    "Minas Gerais", "Mato Grosso do Sul", "Mato Grosso", "Par", "Paraba", "Pernambuco", "Piau", "Paran", "Rio de Janeiro",
    "Rio Grande do Norte", "Rondnia", "Roraima", "Rio Grande do Sul", "Santa Catarina", "Sergipe", "So Paulo", "Tocantins"
  ];
  userParams: any = {};
  pagination: Pagination;
  page: number;

  constructor(
    private userService: UserService,
    private alertify: AlertifyService,
    private route: ActivatedRoute
  ) {}

  ngOnInit() {
    this.route.data.subscribe((data) => {
    this.users = data["users"].result;
    this.pagination = data["users"].pagination;
      
    this.userParams.minAge = localStorage.getItem('minAge') ?? 18;
    this.userParams.maxAge = localStorage.getItem('maxAge') ?? 99;
    this.userParams.uf = localStorage.getItem('uf') ?? 'Acre';
    this.userParams.orderBy = 'lastActive';
    });    
  }

  pageChanged(event: any): void {
    this.pagination.currentPage = event.page;
    this.loadUsers();
  }

  loadUsers() {
    this.userService
      .getUsers(this.pagination.currentPage, this.pagination.itemsPerPage, this.userParams)
      .subscribe((response: PaginatedResult<User[]>) => {
        this.users = response.result;
        this.pagination = response.pagination;
      });
      if (this.userParams.minAge) localStorage.setItem('minAge', this.userParams.minAge);
      if (this.userParams.maxAge) localStorage.setItem('maxAge', this.userParams.maxAge);
      if (this.userParams.uf) localStorage.setItem('uf', this.userParams.uf);
  }

  resetFilters() {
    this.userParams.uf = 'Santa Catarina'; //TODO Todos depois que acertar a paginao
    this.userParams.minAge = 18;
    this.userParams.maxAge = 99;
    this.loadUsers();
  }
}
